import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import Image from 'next/image';
import { monthlyFloraArchive } from '@/data/flowers';

export default function MonthlyFlora() {
  const [isSortedDesc, setIsSortedDesc] = useState(true);

  const sortedArchive = [...monthlyFloraArchive].sort((a, b) => {
    const valA = a.year * 100 + a.month;
    const valB = b.year * 100 + b.month;
    return isSortedDesc ? valB - valA : valA - valB;
  });

  return (
    <div className="section-container !pt-0 max-w-4xl mx-auto">
      {/* Sort Toggle */}
      <div className="flex justify-end mb-12">
        <button 
          onClick={() => setIsSortedDesc(!isSortedDesc)}
          className="text-xs uppercase tracking-widest font-bold text-accent-sage flex items-center gap-2 hover:text-accent-terracotta transition-colors"
        >
          {isSortedDesc ? "Newest First" : "Oldest First"} 
          <span className="text-lg">⇅</span>
        </button>
      </div>

      {/* Timeline */}
      <div className="relative border-l border-background-beige ml-4 md:ml-24 pl-8 md:pl-16 space-y-16 py-8">
        <AnimatePresence mode='popLayout'>
          {sortedArchive.map((item, idx) => (
            <motion.div
              key={item.id}
              initial={{ opacity: 0, x: 20 }}
              whileInView={{ opacity: 1, x: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.6, delay: idx * 0.1 }}
              className="relative"
            >
              {/* Date Badge */}
              <div className="absolute -left-[45px] md:-left-[108px] top-0 flex items-center justify-center w-[34px] h-[34px] md:w-[60px] md:h-[60px] rounded-full bg-accent-sage text-white text-[10px] md:text-sm font-bold shadow-soft">
                {item.month}월
              </div>
              <div className="absolute -left-[48px] md:-left-[111px] top-[40px] md:top-[70px] text-[10px] md:text-xs text-text-warm/40 font-bold uppercase rotate-90 origin-left">
                {item.year}
              </div>

              {/* Card */}
              <div className="bg-white rounded-organic shadow-soft overflow-hidden flex flex-col md:flex-row hover:shadow-xl transition-shadow duration-500">
                <div className="relative w-full md:w-48 aspect-square shrink-0">
                  <Image
                    src={item.image}
                    alt={item.title}
                    fill
                    className="object-cover"
                  />
                </div>
                <div className="p-6 md:p-8 flex flex-col justify-center">
                  <span className="text-[10px] uppercase tracking-widest text-accent-sage font-bold mb-2">
                    {item.year}년 {item.month}월호
                  </span>
                  <h3 className="text-xl md:text-2xl font-serif text-text-warm mb-3">{item.title}</h3>
                  <p className="text-sm text-text-warm/70 leading-relaxed mb-6">
                    {item.summary}
                  </p>
                  <a 
                    href={item.link} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="text-xs uppercase tracking-widest font-bold text-accent-rose hover:underline"
                  >
                    Read More →
                  </a>
                </div>
              </div>
            </motion.div>
          ))}
        </AnimatePresence>
      </div>
    </div>
  );
}
